<template>
  <div class="section">
      <table>
          <tr>
              <td width="10%">1</td>
              <td colspan="2" width="20%">井号</td>
              <td>{{info.well_no}}</td>
          </tr>
          <tr>
              <td>2</td>
              <td colspan="2">井眼</td>
              <td>{{info.well_hold}}</td>
          </tr>
          <tr>
              <td>3</td>
              <td colspan="2">区域</td>
              <td>{{info.area}}</td>
          </tr>
          <tr>
              <td>4</td>
              <td colspan="2">所属油田</td>
              <td>{{info.oil_field}}</td>
          </tr>
          <tr>
              <td>5</td>
              <td colspan="2">井控类别</td>
              <td>{{info.well_control}}</td>
          </tr>
          <tr>
              <td>6</td>
              <td colspan="2">构造名称</td>
              <td>{{info.structure_name}}</td>
          </tr>
          <tr>
              <td>7</td>
              <td colspan="2">构造位置</td>
              <td>{{info.structure_position}}</td>
          </tr>
          <tr>
              <td>8</td>
              <td colspan="2">井型</td>
              <td>{{info.well_shape}}</td>
          </tr>
          <tr>
              <td>9</td>
              <td colspan="2">井别</td>
              <td>{{info.well_type}}</td>
          </tr>
          <tr>
              <td>10</td>
              <td colspan="2">设计单位</td>
              <td>{{info.degisn_unit}}</td>
          </tr>
           <tr>
              <td>11</td>
              <td colspan="2">施工单位</td>
              <td>{{info.construct_unit}}</td>
          </tr>
           <tr>
              <td>12</td>
              <td colspan="2">钻机类型</td>
              <td>{{info.rig_type}}</td>
          </tr>
          <tr>
              <td>13</td>
              <td colspan="2">钻机编号</td>
              <td>{{info.rig_no}}</td>
          </tr>
          <tr>
              <td rowspan="4">14</td>
              <td rowspan="4">井位</td>
              <td>井口横坐标</td>
              <td>{{info.x_axis}}</td>
          </tr>
          <tr>
              <td>井口纵坐标</td>
              <td>{{info.y_axis}}</td>
          </tr>
          <tr>
              <td>地理位置</td>
              <td>{{info.geology_position}}</td>
          </tr>
          <tr>
              <td>测线位置</td>
              <td>{{info.testline_position}}</td>
          </tr>
          <tr>
              <td>15</td>
              <td colspan="2">地面海拔高度</td>
              <td>{{info.above_sea_level}}</td>
          </tr>
          <tr>
              <td>16</td>
              <td colspan="2">转盘面海拔（补心）高度（m）</td>
              <td>{{info.bushing}}</td>
          </tr>
          <tr>
              <td>17</td>
              <td colspan="2">磁偏角（°）</td>
              <td>{{info.magnetic_declination}}</td>
          </tr>
          <tr>
              <td>18</td>
              <td colspan="2">钻探目的</td>
              <td>{{info.drill_target}}</td>
          </tr>
          <tr>
              <td>19</td>
              <td colspan="2">完钻层位</td>
              <td>{{info.complete_layer}}</td>
          </tr>
          <tr>
              <td>20</td>
              <td colspan="2">完钻方式</td>
              <td>{{info.complete_method}}</td>
          </tr>
          <tr>
              <td>21</td>
              <td colspan="2">完钻原则</td>
              <td>{{info.complete_principle}}</td>
          </tr>
      </table>
  </div>
</template>

<script>
import request from "@/utils/request";
export default {
  props: ["route"],
  data() {
    return {
      info: {}
    };
  },

  components: {},

  computed: {},

  mounted: function() {
   
    let data = this.route;
    // console.log(data instanceof Object);  //true
    request({
      url: "/data/query/wellbasedata",
      method: "post",
      data
    }).then(res => {
      this.info = res[0];
    });
  },

  methods: {}
};
</script>



